
import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';


import { WishlistContext} from './../../Context/WishListContext';
import toast from 'react-hot-toast';
import { CartContext } from '../../Context/CartContext';
export default function Products() {
  let {addToWishList}=useContext(WishlistContext)
  async function postToWishlist(id){
    let {data}=await addToWishList(id)
    console.log(data);
    toast.success(data.message,{
      duration:2000
  })
  }
  let {addToCart}=useContext(CartContext)
  async function postToCart(id){
    let {data}= await addToCart(id)
    console.log(data);
    toast.success(data.message,{
      duration:2000
  })
  }
//  async function colorHeart(id){
//     let {data}=await addToWishList(id)
//     if(data.message==='success'){
//       color:red

//     }

//   }
  const [products,setProducts]=useState([])
  const [spinner,setSpinner]=useState(false)
  async function getProducts(){
    

    setSpinner(true)
    let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products`);
    setProducts(data.data);
    setSpinner(false)
  }
  
  useEffect(()=>{
    getProducts()
    
  },[])
  return (
    <div className='row my-4 pt-5'>
      <Helmet>
        <meta charSet='utf-8'/>
        <title>Products</title>
    </Helmet>
     {spinner?<BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />:<>
     {products.map(product=>
      <div key={product.id} className='col-lg-3'>
          <div className='product p-2'>
          <Link to={`/productdetails/${product.id}`}className='a'>
              <img src={product.imageCover} alt='product' className='w-100'/>
              <h2 className='h5'>{product.title.split(' ').splice(0,2).join(' ')}</h2>
              <h2 className='h5'>{product.price}$</h2>
              </Link>
              <div>
              <span><i className="fa-regular fa-heart fa-xl m-4 " onClick={() => postToWishlist(product.id)}></i></span>
              <button className='btn bg-main text-light' onClick={()=> postToCart(product.id)} >add to cart</button>
             


              </div>
              
          </div>
      </div>
      )}
     </> 
     }
     
     </div>
  );
}
