import React, { useEffect, useState } from 'react'
import { useContext } from 'react'
import { CartContext } from '../../Context/CartContext'
import { BallTriangle } from 'react-loader-spinner'
import { Link } from 'react-router-dom';

export default function Cart() {
    const [cartItems,setCartItems]=useState([])
    const [spinner,setSpinner]=useState(true)
    let {getCartItems,deleteCartItem,updataCartItems}=useContext(CartContext)
    async function updateCart(id,count){
        let {data} = await updataCartItems(id,count)
        if(count<1){
            deleteItems(id)
        }else{

            console.log(data);
         setCartItems(data)
         setSpinner(false)
        }
        

    }
    
    async function CartItems(){
        let {data} = await getCartItems()
        console.log(data);
        setCartItems(data)
        setSpinner(false)

    }
    async function deleteItems(id){
        setSpinner(true)
        let {data} = await deleteCartItem(id)

        console.log(data);
        setCartItems(data)
        setSpinner(false)

    }
    useEffect(()=>{
        CartItems()
    },[])
  return (
    <div className='bg-main-light p-2 mt-5'>
        
      {spinner?<BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />:cartItems?<>
        <p className='text-main mt-3'>Number of items :{cartItems.numOfCartItems}</p>
        <p className='text-main'>Total cart price :{cartItems.data.totalCartPrice}</p>
        <Link to={`/shipingaddress/${cartItems.data._id}`} className='btn bg-main text-light m-3'>Online Payment</Link>
        {cartItems.data.products.map(product=>
        <div className='row main-border p-2 align-items-center' key={product.product.id}>
        <div className='col-md-2'>
        <img src={product.product.imageCover} alt={product.product.title} className='w-75'/>
        </div>
       
        <div className='col-md-9'>
            <h3 className='h5'>{product.product.title.split(' ').splice(0,2).join(' ')}</h3>
            <p className='text-main'>Price : {product.price}</p>
            <button className='btn text-danger' onClick={()=>deleteItems(product.product.id)}><i className='fas fa-trash'></i>Remove</button>
        </div>
        <div className='col-md-1 '>
            <div className='d-flex'>
                <button className='btn bg-main text-light p-2' onClick={()=>updateCart(product.product.id,product.count+1)}>+</button>
                <span className='mt-2'>{product.count}</span>
                <button className='btn bg-main text-light p-2'  onClick={()=>updateCart(product.product.id,product.count-1)}>-</button>
            </div>
        </div>
       
        
        </div>

    

            )}
        
        

</>:<h2>Cart is Empty</h2>}

    </div>
  )
}
