import React, { useState } from 'react'
import * as Yup from 'yup';
import { useFormik } from 'formik';
import axios from 'axios';
import { Link, useNavigate } from "react-router-dom";
export default function Register() {
  let [loading,setLoading]=useState(false)
  let [apiError,setApiError]=useState(false)
  let navigate=useNavigate()
  let userSchema=Yup.object({
    name:Yup.string().max(10,"Name is to long").min(3,"name is too short").required("please enter your name"),
    email:Yup.string().email("email is invalid").required("please enter your mail"),
    // 123 456 7890
    
    password:Yup.string().matches(/^[A-Z][\w @]{5,8}$/,"invalid password must start with upperCase").required("please eneter you password"),
    rePassword:Yup.string().oneOf([Yup.ref("password")],"password and repassword does not match").required("please enter your password"),
    phone:Yup.string().matches(/^01[0125][0-9]{8}$/,"inivalid phone").required("please enter your phone")
  })
   async function registerSubmit(values){
    setLoading(true)
   let {data}= await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup` ,values)
   .catch((err)=>{
    setApiError(err.response.data.message)
    setLoading(false)
  })
   if(data.message === 'success'){
    setLoading(false)
    navigate("/login")
   }
  }
  let formik =useFormik({
    initialValues:{
      name:"",
      email:"",
      password:"",
      rePassword:"",
      phone:""
    },validationSchema:userSchema,onSubmit:registerSubmit
  })   
  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
    {apiError?<div className='alert alert-danger'>{apiError}</div>:""}
    <h2>Register Now</h2>
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor='name'>Name :</label>
        <input type='text' id='name' name='name' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.name}/>
        {formik.errors.name&&formik.touched.name?<div className='alert alert-danger p-2 mt2'>{formik.errors.name}</div>:''}
        <label htmlFor='email'>Email :</label>
        <input type='email' id='email' name='email' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.email}/>
        {formik.errors.email&&formik.touched.email?<div className='alert alert-danger p-2 mt2'>{formik.errors.email}</div>:''}
        <label htmlFor='password'>Password :</label>
        <input type='password' id='password' name='password' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.password}/>
        {formik.errors.password&&formik.touched.password?<div className='alert alert-danger p-2 mt2'>{formik.errors.password}</div>:''}
        <label htmlFor='rePassword'>Repassword :</label>
        <input type='password' id='rePassword' name='rePassword' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.rePassword}/>
        {formik.errors.rePassword&&formik.touched.rePassword?<div className='alert alert-danger p-2 mt2'>{formik.errors.rePassword}</div>:''}
        <label htmlFor='phone'>Phone :</label>
        <input type='tel' id='phone' name='phone' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.phone}/>
        {formik.errors.phone&&formik.touched.phone?<div className='alert alert-danger p-2 mt2'>{formik.errors.phone}</div>:''}
        {loading?<button type='button' className='btn bg-main text-light m-3'>
        <i className='fas fa-spinner fa-spin'></i>
        </button>:
        <button type='submit' className='btn bg-main text-light m-3' disabled={!(formik.dirty&&formik.isValid)}>Register</button>}
        <Link to='/login'>Login Now</Link>
      </form>
    </div>
  )
  }
