import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getSpecificCategory } from '../../Redux/CategoryDetailSlice';
import { BallTriangle } from 'react-loader-spinner';
import { useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
// import { Helmet } from 'react-helmet';

export default function CategoryDetails() {
    const { isLoading,categortdetails} = useSelector(({ categoryDetail }) => categoryDetail);
    const dispatch = useDispatch();
    let {id}=useParams()

    useEffect(() => {
        dispatch(getSpecificCategory(id));
    }, [dispatch,id]);

    return (
        <div className='pt-5'>
            {isLoading ? (
                <BallTriangle
                    height={100}
                    width={100}
                    radius={5}
                    color="#4fa94d"
                    ariaLabel="ball-triangle-loading"
                    wrapperStyle={{}}
                    wrapperClass="d-flex mt-5 justify-content-center"
                    visible={true}
                />
            ) : (
                <div>
                  <Helmet>
                <meta charSet='utf-8'/>
                <title>{categortdetails.name}</title>
            </Helmet>
                  {categortdetails&& (
                    <div className='row my-4 d-flex justify-content-center product'>
                      <div className='col-md-3'>
                        <img src={categortdetails.image} alt='Product Cover' className='w-100'/>

                        </div>
                        <div className='col-md-9 my-3'>
                        <h3 className='text-center text-main'>{categortdetails.name}</h3>
                        </div>
                      
                      
                    </div>
                  )}
                </div>
              )}
        </div>
    );
}
