import React, {useState } from 'react'
import * as Yup from 'yup';
import { useFormik } from 'formik';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
export default function ResetPassword() {
  let [loading,setLoading]=useState(false)
  let [apiError,setApiError]=useState(false)
  let navigate=useNavigate()
  let userSchema=Yup.object({
    newPassword:Yup.string().matches(/^[A-Z][\w @]{5,8}$/,"invalid password must start with upperCase").required("please eneter you password"),
    email:Yup.string().email("email is invalid").required("please enter your mail"),
  })
  async function registerSubmit(values) {
    try {
        let response = await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`, values);
            setLoading(false);
            navigate("/login");
            console.log(response)
    } catch (error) {
        if (error.response) {
            setApiError(error.response.data.message); // Handle API response error
        } else {
            setApiError('Network error. Please try again later.'); // Handle network error
        }
        setLoading(false);
    }
}
  let formik =useFormik({
    initialValues:{
        email:"",
        newPassword:"",
    },validationSchema:userSchema,onSubmit:registerSubmit
  })   
  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
    {apiError?<div className='alert alert-danger'>{apiError}</div>:""}
    <h2>Reset Password</h2>
      <form onSubmit={formik.handleSubmit}>
      <label htmlFor='email'>Email :</label>
        <input type='email' id='email' name='email' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.email}/>
        {formik.errors.email&&formik.touched.email?<div className='alert alert-danger p-2 mt2'>{formik.errors.email}</div>:''}
        <label htmlFor='password'>Password :</label>
        <input type='password' id='newPassword' name='newPassword' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.newPassword}/>
        {formik.errors.newPassword&&formik.touched.newPassword?<div className='alert alert-danger p-2 mt2'>{formik.errors.newPassword}</div>:''}

        {loading?<button type='button' className='btn bg-main text-light m-3'>
        <i className='fas fa-spinner fa-spin'></i>
        </button>:
        <button type='submit' className='btn bg-main text-light m-3' disabled={!(formik.dirty&&formik.isValid)}>Reset</button>}
        
      </form>
    </div>
  )
  }
