import React, { useEffect, useState } from 'react'
import { useContext } from 'react'

import { BallTriangle } from 'react-loader-spinner'
import { WishlistContext } from '../../Context/WishListContext'
import { CartContext } from '../../Context/CartContext'
import toast from 'react-hot-toast'

export default function WishList() {
  let {addToCart}=useContext(CartContext)
  async function postToCart(id){
    let {data}= await addToCart(id)
    console.log(data);
    toast.success(data.message,{
      duration:2000
  })
  }
    const [wishItems,setWishItems]=useState([])
    const [spinner,setSpinner]=useState(true)
    let {getWishListItems,deleteWishItem}=useContext(WishlistContext);
    async function fetchToWishList(){
        let {data} = await getWishListItems()
        console.log(data);
        setWishItems(data)
        setSpinner(false)

    }
    async function deleteItems(id){
        setSpinner(true)
        let {data} = await deleteWishItem(id)

        console.log(data);
        setWishItems(data)
        setSpinner(false)

    }
    useEffect(()=>{
        fetchToWishList()
    },[])
  return (
    <div className='bg-main-light p-2 mt-5'>
        
      {spinner?<BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />:<>
        {wishItems.data.map((product,index)=><div className='row m-3 main-border align-items-center' key={index}> 
          <div className='col-md-2'>
            <img src={product.imageCover} alt='product' className='w-75'/>
          </div>
          <div className='col-md-6'>
          <h5 className='text-main'>{product.title}</h5>
          <h5 className='text-main'>price : {product.price} EGP</h5>
          <button className='btn text-danger' onClick={()=>deleteItems(product.id)}><i className='fas fa-trash'></i>Remove</button>

          
          </div>
          <div className='col-md-4'>
          <button className='btn bg-main text-light' onClick={()=> postToCart(product.id)} >add to cart</button>
          </div>

        </div>)}
</>}
    </div>
  )
}
