import './App.css';
import { createHashRouter, RouterProvider } from "react-router-dom";
import Layout from './components/Layout/Layout';
import Home from './components/Home/Home';
import Products from './components/Products/Products';
import Brands from './components/Brands/Brands';
import Categories from './components/Categories/Categories';
import Login from './components/Login/Login';
import Register from './components/Register/Register';
import { useContext, useEffect } from 'react';
import { UserContext } from './Context/UserContext';
import ProtectedRoute from './components/ProtectedRoute/ProtectedRoute';
import ProductDetails from './components/ProductDetails/ProductDetails';
import { Toaster } from 'react-hot-toast';
import Cart from './components/Cart/Cart';
import BrandDetails from './components/Brands/BrandDetails';
import CategoryDetails from './components/Categories/CategoryDetails';
import ForgetPassword from './components/ForgetPassword/ForgetPassword.jsx';
import VerificationCode from './components/ForgetPassword/VerficationCode.jsx';
import ResetPassword from './components/ForgetPassword/ResetPassword.jsx';
import WishList from './components/WishList/WishList.jsx';
import ShippingAddress from './components/ShippingAddress/ShippingAddress.jsx';
import NotFound from './components/NotFound/NotFound.jsx';

function App() {
  let routers = createHashRouter([
    {
      path: '',
      element: <Layout />,
      children: [
        { index: true, element: <ProtectedRoute><Home /></ProtectedRoute> },
        { path: "product", element: <ProtectedRoute><Products /></ProtectedRoute> },
        { path: "productdetails/:id", element: <ProtectedRoute><ProductDetails /></ProtectedRoute> },
        { path: "categories", element: <ProtectedRoute><Categories /></ProtectedRoute> },
        { path: "brand", element: <ProtectedRoute><Brands /></ProtectedRoute> },
        { path: "cart", element: <ProtectedRoute><Cart /></ProtectedRoute> },
        { path: "branddetails/:id", element: <ProtectedRoute><BrandDetails /></ProtectedRoute> },
        { path: "categorydetails/:id", element: <ProtectedRoute><CategoryDetails /></ProtectedRoute> },
        { path: "shipingaddress/:cartId", element: <ProtectedRoute><ShippingAddress/></ProtectedRoute> },
        { path: "wishlist", element: <ProtectedRoute><WishList/></ProtectedRoute> },
        { path: "forgotPasswords", element: <ForgetPassword /> },
        { path: "/verifyCode", element: <VerificationCode/> }, 
        { path: "/resetpassword", element: <ResetPassword/> },
        { path: "login", element: <Login /> },
        { path: "register", element: <Register /> },
        { path: "*", element: <NotFound/> },
      
        
      ]
    }
  ]);

  let { setUserToken } = useContext(UserContext);
  useEffect(() => {
    if (localStorage.getItem('userToken')) {
      setUserToken(localStorage.getItem('userToken'))
    }
  }, [])

  return (
    <div>
      <RouterProvider router={routers}></RouterProvider>
      <Toaster />
    </div>
  );
}

export default App;
