import React, { useContext, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import logo from '../../Assets/images/freshcart-logo.svg'
import { UserContext } from '../../Context/UserContext'
import { CartContext } from './../../Context/CartContext';


export default function Navbar() {
  const [cartItems,setCartItems]=useState([])
  let navigate = useNavigate()
  let { userToken, setUserToken } = useContext(UserContext)
  let {getCartItems}=useContext(CartContext)

  function logOut() {
    localStorage.removeItem('userToken')
    setUserToken(null)
    navigate("/login")
  }
  async function CartItems(){
    let {data} = await getCartItems()
    console.log(data);
    setCartItems(data)
    

}

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-light fixed-top">
        <div className="container-fluid">
          <Link className="navbar-brand" href="/"><img src={logo} alt='fresh Cart'/> </Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {userToken != null ? (
                <>
                  <li className="nav-item active">
                    <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="product">Products</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="categories">Categories</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="brand">Brands</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="wishlist">Wishlist</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="cart" onClick={CartItems}><i className="fa-solid fa-cart-shopping mt-1" ></i>{cartItems.numOfCartItems}</Link>
                  </li>
                </>
              ) : null}
            </ul>
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item d-flex align-items-center">
                <i className='fab mx-2 fa-facebook'></i>
                <i className='fab mx-2 fa-twitter'></i>
                <i className='fab mx-2 fa-instagram'></i>
                <i className='fab mx-2 fa-youtube'></i>
                <i className='fab mx-2 fa-tiktok'></i>
              </li>
              {userToken != null ? (
                <li className="nav-item" onClick={logOut}>
                  <Link className="nav-link">Logout</Link>
                </li>
              ) : (
                <>
                  <li className="nav-item">
                    <Link className="nav-link" to="login">Login</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="register">Register</Link>
                  </li>
                </>
              )}
            </ul>
          </div>
        </div>
      </nav>
    </div>
  )
}
