import React, { useContext, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getProductDetails } from '../../Redux/ProductDetailsSlice';
import { useParams } from 'react-router-dom';
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
import { CartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';

export default function ProductDetails() {
  let {addToCart}=useContext(CartContext)
  async function postToCart(id){
    let {data}= await addToCart(id)
    console.log(data);
    toast.success(data.message,{
      duration:2000
  })
  }
  let { id } = useParams();
  let dispatch = useDispatch();
  let { productDetail, isLoading } = useSelector(({ productDetails }) => productDetails);

  useEffect(() => {
    dispatch(getProductDetails(id)); 
  }, [dispatch, id]);

  return (
    <div className='pt-5'>
      {isLoading ? (
        <BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />
      ) : (
        <div>
          <Helmet>
        <meta charSet='utf-8'/>
        <title>{productDetail.title}</title>
    </Helmet>
          {productDetail && (
            <div className='row my-4'>
              <div className='col-md-3'>
                <img src={productDetail.imageCover} alt='Product Cover' className='w-100'/>
                
                </div>
                <div className='col-md-9 d-flex align-items-center flex-column my-4'>
                 <h3 className='m-4'>{productDetail.title}</h3>
                  <p className='m-4'>{productDetail.description}</p>
                  <h3 className='m-4'>{productDetail.price}$</h3>
                  <button className='btn bg-main text-light m-4' onClick={()=> postToCart(id)}>Add to cart</button>

                </div>
              
              
            </div>
          )}
        </div>
      )}
    </div>
  );
}
