import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import axios from 'axios'
export let initialState={brands:[],isLoading:false,error:null}
export let getAllBrands=createAsyncThunk('brandSlice/getAllBrands',
async ()=>{
    let {data}=await axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
    .catch((err)=>err)
    return data.data
    

})
export let BrandSlice=createSlice({
    name:"brandSlice",
    initialState,
    extraReducers:(builder)=>{
        builder.addCase(getAllBrands.pending,(state,action)=>{
            state.isLoading=true
        });
        builder.addCase(getAllBrands.fulfilled,(state,action)=>{
            state.brands=action.payload
            state.isLoading=false

        })

    }
})
export let BrandsReducer=BrandSlice.reducer