import React from 'react'
import Slider from "react-slick";
import slider1 from '../../Assets/images/slider1.jpg'
import slider2 from '../../Assets/images/slider2.jpg'
import slider3 from '../../Assets/images/slider3.jpg'
import image1 from '../../Assets/images/img1.jpg'
import image2 from '../../Assets/images/img2.jpg'


export default function MainSlider() {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
      };
  return (
    <div className='row m-4 g-0'>
        <div className='col-md-9'>
        <Slider {...settings}>
            <img src={slider1} alt='slider' className='w-100' height={"400px"}/>
            <img src={slider2} alt='slider' className='w-100'  height={"400px"}/>
            <img src={slider3} alt='slider' className='w-100'  height={"400px"}/>
        </Slider>
        </div>
        <div className='col-3'>
            <img src={image1} alt='product' height={"200px"} className='w-100'/>
            <img src={image2} alt='product' height={"200px"} className='w-100'/>

        </div>
    </div>
  )
}
