import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getAllBrands } from '../../Redux/BrandSlice';
// import Brands from './Brands';
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';

export default function Brands() {
  const dispatch = useDispatch();
 let {isLoading,brands}=useSelector(({brand})=>brand)
  useEffect(() => {
    dispatch(getAllBrands());
  }, []); 

  return (
    <div className='pt-5'>
      <Helmet>
      <meta charSet='utf-8'/>
        <title>Brands</title>
    </Helmet>
      {isLoading?<BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />:<div className='row py-5'>
      {brands.map(brand=>
      <div className='col-md-3 product' key={brand._id}>
        <img src={brand.image} alt={brand.name} className='w-100'/>
        <p>{brand.name}</p>

      </div>
        )}
      </div>
      }
    </div>
  );
}
