import React, { useContext} from 'react'
import * as Yup from 'yup';
import { useFormik } from 'formik';
import { useParams } from 'react-router-dom';
import { CartContext } from '../../Context/CartContext';



export default function ShippingAddress() {
    let {cartId}=useParams()
    console.log(cartId);
  let {checkOutSession}=useContext(CartContext)
  let checkOut=Yup.object({
    details:Yup.string().required("please enter"),
    phone:Yup.string().matches(/^01[0125][0-9]{8}$/,"inivalid phone").required("please enter your phone"),
    city:Yup.string().required("please enter"),
  })
   async function ShippingAddress(values){
    
    let {data}=await checkOutSession(cartId,values)
    if (data.status==='success'){
        window.location.href=data.session.url

    }

        console.log(data)
  }
  let formik =useFormik({
    initialValues:{
     
     details:"",
     phone:""
    },validationSchema:checkOut,onSubmit:ShippingAddress
  })   
  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
    {/* {apiError?<div className='alert alert-danger'>{apiError}</div>:""} */}
    <h2>Address:</h2>
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor='details'>details :</label>
        <input type='text' id='details' name='details' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.details}/>
        {formik.errors.details&&formik.touched.details?<div className='alert alert-danger p-2 mt2'>{formik.errors.details}</div>:''}
        <label htmlFor='phone'>Phone :</label>
        <input type='tel' id='phone' name='phone' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.phone}/>
        {formik.errors.phone&&formik.touched.phone?<div className='alert alert-danger p-2 mt2'>{formik.errors.phone}</div>:''}
        <label htmlFor='city'>city :</label>
        <input type='text' id='city' name='city' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.city}/>
        {formik.errors.city&&formik.touched.city?<div className='alert alert-danger p-2 mt2'>{formik.errors.city}</div>:''}
        <button type='submit' className='btn bg-main text-light m-3' disabled={!(formik.dirty&&formik.isValid)}>Submit</button>
  
        
      </form>
    
    </div>
  )
  }
