import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';
export let initialState={categories:[],isLoading:false,error:null}
export let getAllCategories=createAsyncThunk('categorySlice/getAllCategories',
async()=>{
    let {data}= await axios.get('https://ecommerce.routemisr.com/api/v1/categories')
    return data.data

})
export let CategorySlice=createSlice({
    name:'categorySlice',
    initialState,
    extraReducers:(builder)=>{
        builder.addCase(getAllCategories.pending,(state,action)=>{
            state.isLoading=true
        })
        builder.addCase(getAllCategories.fulfilled,(state,action)=>{
            state.categories=action.payload
            state.isLoading=false
        })
    }
    

})
export let CategoryReducer=CategorySlice.reducer