import React, { useContext, useState } from 'react'
import * as Yup from 'yup';
import { useFormik } from 'formik';
import axios from 'axios';
import { Link, useNavigate } from "react-router-dom";
import { UserContext } from '../../Context/UserContext';



export default function Login() {
  let {setUserToken}=useContext(UserContext)
  let [loading,setLoading]=useState(false)
  let [apiError,setApiError]=useState(false)
  let navigate=useNavigate()
  let userSchema=Yup.object({
    email:Yup.string().email("email is invalid").required("please enter your mail"),
    password:Yup.string().matches(/^[A-Z][\w @]{5,8}$/,"invalid password must start with upperCase").required("please eneter you password"), 
  })
   async function registerSubmit(values){
    setLoading(true)
   let {data}= await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signin` ,values)
   .catch((err)=>{
    setApiError(err.response.data.message)
    setLoading(false)
  })
   if(data.message === 'success'){
    setLoading(false)
    localStorage.setItem("userToken",data.token)
    setUserToken(data.token)
    navigate("/")
   }
  }
  let formik =useFormik({
    initialValues:{
     
      email:"",
      password:""
    },validationSchema:userSchema,onSubmit:registerSubmit
  })   
  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
    {apiError?<div className='alert alert-danger'>{apiError}</div>:""}
    <h2>Login Now</h2>
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor='email'>Email :</label>
        <input type='email' id='email' name='email' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.email}/>
        {formik.errors.email&&formik.touched.email?<div className='alert alert-danger p-2 mt2'>{formik.errors.email}</div>:''}
        <label htmlFor='password'>Password :</label>
        <input type='password' id='password' name='password' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.password}/>
        {formik.errors.password&&formik.touched.password?<div className='alert alert-danger p-2 mt2'>{formik.errors.password}</div>:''}
        {loading?<button type='button' className='btn bg-main text-light m-3'>
        <i className='fas fa-spinner fa-spin'></i>
        </button>:
        <button type='submit' className='btn bg-main text-light m-3' disabled={!(formik.dirty&&formik.isValid)}>Login</button>}
        
      </form>
      <Link to='/register' className='btn bg-main text-light m-3'>Register Now</Link>
      <Link to='/forgotPasswords' className='btn bg-main text-light m-3'>Forget Password?</Link>
    </div>
  )
  }
