import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getAllCategories } from '../../Redux/CategorySlice'
// import ClipLoader from "react-spinners/ClipLoader";
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';

export default function Categories() {
  let dispatch=useDispatch()
        
  let {categories,isLoading}=useSelector(({category})=>category)
  useEffect(()=>{
    dispatch(getAllCategories())
  },[])

  return (
    <div className='pt-5'>
      <Helmet>
        <meta charSet='utf-8'/>
        <title>Categories</title>
    </Helmet>
      {isLoading?<BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass="d-flex mt-5 justify-content-center"
        visible={true}
        />:<div className='row py-5'>
      {categories.map(category=>
      
          <div className='col-md-3 product' key={category._id}>
            <Link className='a' to={`/categorydetails/${category._id}`}>
            <img src={category.image} alt={category.name} className='w-100' height={"250px"}/>
            <h4 className='text-main'>{category.name}</h4>
            </Link>
          </div>
      
      )}
      </div>
      }
    </div>
  );
}
