import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export let initialState={productDetail:[],isLoading:false,error:null};
export let getProductDetails=createAsyncThunk('ProductDetailsSlice/getProductDetails',
async (id) => {  
    let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
    return data.data
})

export let ProductDetailsSlice=createSlice({
    name:"productDetails",
    initialState,
    extraReducers:(builder)=>{
        builder.addCase(getProductDetails.pending,(state,action)=>{
            state.isLoading=true
        });
        builder.addCase(getProductDetails.fulfilled,(state,action)=>{
            state.productDetail=action.payload
            state.isLoading=false
        })
    }
})

export let productDetailsReducer=ProductDetailsSlice.reducer
