import React, { useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import axios from 'axios';
import { useNavigate } from "react-router-dom";

export default function ForgetPassword() {
  let navigate = useNavigate();
  let [loading, setLoading] = useState(false);
  let [apiError, setApiError] = useState(false);

  let userSchema = Yup.object({
    email: Yup.string().email("email is invalid").required("please enter your mail"),
  });

  async function registerSubmit(values) {
    setLoading(true);
    try {
      let response = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords`, values);
      console.log("API Response:", response.data); // Log the response data
      if (response.data.statusMsg === 'success') {
        setLoading(false);
        navigate("/verifyCode");
      }
    } catch (error) {
      setApiError(error.response.data.message);
      setLoading(false);
    }
  }
  

  let formik = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: userSchema,
    onSubmit: registerSubmit
  });

  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
      {apiError ? <div className='alert alert-danger'>{apiError}</div> : ""}
      <h2>Login Now</h2>
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor='email'>Email :</label>
        <input type='email' id='email' name='email' onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control mb-2' value={formik.values.email} />
        {formik.errors.email && formik.touched.email ? <div className='alert alert-danger p-2 mt2'>{formik.errors.email}</div> : ''}
        {loading ? <button type='button' className='btn bg-main text-light m-3'><i className='fas fa-spinner fa-spin'></i></button> :
          <button type='submit' className='btn bg-main text-light m-3' disabled={!(formik.dirty && formik.isValid)}>Submit</button>
        }
      </form>
    </div>
  );
}
