import React, { useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

export default function VerificationCode() {
  const [loading, setLoading] = useState(false);
  const [apiError, setApiError] = useState('');

  const navigate = useNavigate();

  const userSchema = Yup.object({
    resetCode: Yup.string().required('Please enter the reset code'),
  });

  async function registerSubmit(values) {
    setLoading(true);
    try {
      const response = await axios.post(
        `https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode`,
        values
      );
      if (response.data.status === 'Success') {
        
        navigate('/resetpassword');
      } else {
        setApiError('Verification code is incorrect.');
      }
    } catch (error) {
      setApiError(error.data.message);
    } finally {
      setLoading(false);
    }
  }

  const formik = useFormik({
    initialValues: {
       resetCode: '',
    },
    validationSchema: userSchema,
    onSubmit: registerSubmit,
  });

  return (
    <div className='w-75 mx-auto py-4 pt-5 mt-5'>
      {apiError && <div className='alert alert-danger'>{apiError}</div>}
      <h2>Enter Verification Code</h2>
      <form onSubmit={formik.handleSubmit}>
        <label htmlFor='resetCode'>Verification Code:</label>
        <input
          type='text'
          id='resetCode'
          name='resetCode'
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          className='form-control mb-2'
          value={formik.values.resetCode}
        />
        {formik.errors.resetCode && formik.touched.resetCode && (
          <div className='alert alert-danger p-2 mt2'>{formik.errors.resetCode}</div>
        )}
        {loading ? (
          <button type='button' className='btn bg-main text-light m-3' disabled>
            <i className='fas fa-spinner fa-spin'></i> Loading...
          </button>
        ) : (
          <button type='submit' className='btn bg-main text-light m-3' disabled={!formik.isValid}>
            Submit
          </button>
        )}
      </form>
    </div>
  );
}
