import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export let initialState={categortdetails:[],isLoading:false}
export let getSpecificCategory=createAsyncThunk('categorydetalieSlice/getSpecificCategory',
async(id)=>{
    let {data}=await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}`)
    return data.data
}
)
export let CategoryDetailSlice=createSlice({
    name:'categorydetalieSlice',
    initialState,
    extraReducers:(builder)=>{
        builder.addCase(getSpecificCategory.pending,(state,action)=>{
           state.isLoading=true
        })
        builder.addCase(getSpecificCategory.fulfilled,(state,action)=>{
           state.categortdetails=action.payload
            state.isLoading=false
        })

    }
})
export let CategoryDetailsReducer=CategoryDetailSlice.reducer